<template>
	<div class="exchange-options">
		<div class="window light trigger-block" @click="triggerDropdown">
			<label style="color: #EAEAEA;">{{ $localizeFilter(`exchange_order_type_${options[selectedIndex].replace('-',
				'_')}`) }}</label>
			<button class="trigger-button">
				<img style="width:15px; height: 16px;" :src="require('@/assets/newImages/double-arrows-white.svg')">
			</button>
		</div>
		<div class="window light dropdown-block" v-if="isOpen">
			<ul>
				<li v-for="(option, idx) in options" :key="idx" :class="selectedIndex == idx ? 'selected' : ''"
					@click="selectOption(idx)">
					{{ $localizeFilter(`exchange_order_type_${option.replace('-', '_')}`) }}
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
	props: [
		'options'
	],
	data: () => ({
		isOpen: false,
		selectedIndex: 0,
	}),
	setup() {

	},
	methods: {
		triggerDropdown() {
			this.isOpen = !this.isOpen
		},
		selectOption(idx) {
			this.selectedIndex = idx
			this.$emit('selectOption', idx)
			this.isOpen = false
		}
	}
})
</script>

