<script>
import { defineComponent } from '@vue/composition-api'
import ExchangeTypeOptions from '@/components/exchange/ExchangeTypeOptions.vue'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue, minLength, maxLength } from "@vuelidate/validators"
import localizeFilter from '@/filters/localize.filter'

function multiplyNumberByDecimals(value, decimalsNumber) {
	// const decimals = decimalsNumber.toString().length
	let [integer, fraction] = value.toString().split('.')
	// console.log(decimalsNumber)
	let res = 0n;
	if (fraction) {
		res += BigInt(fraction) * decimalsNumber;
		for (let i = 0; i < fraction.length; i++) {
			res /= BigInt(10);
		}
	}
	res += BigInt(integer) * decimalsNumber;
	return res;
}

export default defineComponent({
	props: {
		type: {
			type: String,
			required: true
		},
		actionType: {
			type: String,
			required: true
		},
		coinsPair: {
			required: true
		},
	},
	components: {
		ExchangeTypeOptions
	},
	validations() {
		return {
			amount: {
				required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
				numeric,
				minValue: minValue(this.minAmount),
			},
			limit: {
				// required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
				numeric,
				minValue: minValue(this.minLimit),
			},
			// trigger: {
			//     required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
			//     numeric,
			//     minValue: minValue(this.minTrigger),
			// }
		};
	},
	setup() {
		return { v$: useVuelidate() };
	},
	data: () => ({
		//   orderTypesNew: ['till_cancelled'],
		//   selectedOrderTypeNew: 'till_cancelled',
		amount: null,
		minAmount: 0,
		limit: null,
		minLimit: 0,
		trigger: null,
		minTrigger: 0,
		errorMessage: null,
		buttonKey: 1
	}),
	computed: {
		assetDecimalsMap() {
			return this.$store.getters.assetDecimalsMap;
		},
	},
	methods: {
		//   selectOptionNew(idx) {
		//     this.selectedOrderTypeNew = this.orderTypesNew[idx]
		//   },
		async submitHandler() {
			this.v$.$touch();
			if (this.v$.$error) {
				setTimeout(() => {
					this.buttonKey += 1
				}, 1000)
				return;
			}

			const el = document.getElementById('exchangeBuyButton');
			el.classList.add("button--loading");

			let { firstCoin: asset, lastCoin: value } = this.coinsPair;

			// // console.log(asset, value)

			let amount = multiplyNumberByDecimals(this.amount, this.assetDecimalsMap[asset]).toString();
			let limit = this.limit ? multiplyNumberByDecimals(+this.limit, this.assetDecimalsMap[value]).toString() : undefined;

			const data = { type: this.type, actionType: this.actionType, amount, limit: limit, trigger: this.trigger, pair: `${asset}/${value}` }

			try {
				// // console.log('submitHandler')
				await this.$store.dispatch('postOrder', data)
				this.$message(localizeFilter('order_posted_successfully'))
				this.errorMessage = null
				setTimeout(() => {
					el.classList.remove("button--loading")
					this.buttonKey += 1
				}, 1000)
			}
			catch (e) {
				setTimeout(() => {
					// console.log(e)
					this.$nextTick(() => {
						el.classList.remove("button--loading")
						this.buttonKey += 1
						// generate message by code
						if (e.code) {
							if (e.code === 1) {
								this.errorMessage = localizeFilter('not_enough_funds')
							} else {
								this.errorMessage = localizeFilter(e.code)
							}
						} else {
							// set message directly from error
							this.errorMessage = e.message
						}
					})
				}, 1000)

			}
		}
	}
})
</script>

